import axios from 'axios';
import Card from '../components/Card';
import AddDialog from '../components/AddDialog';

export default {
  data() {
    return {
      cards: [],
      addDialog: false,
      client: 'Internal'
    }
  },
  mounted() {
    this.readData();
  },
  methods: {
    readData() {
      this.cards = [];
      let url = 'https://api.sheety.co/ec400a6bb2ac250558c262e5fab58060/hfarmData/foglio1';
    
      axios.get(url).then(response => {
        response.data.foglio1.forEach(element => {
          this.cards.push(element);
        });
      })
    },

    openDialog (opt) {
      this.addDialog = opt;
    },

    onChange(event) {
      this.client = event.target.value;
    },

    createNewCard() {
      var descr = document.getElementById("DescrInput").value;
      var fav = document.getElementById("FavCheck").checked;
      var client = document.getElementById("ClientSelect").value;

      if (client === "External")
        client = document.getElementById("ClientNameInput").value;

      if (fav)
        fav = 'X'
      else
        fav = ''
      
      let url = 'https://api.sheety.co/ec400a6bb2ac250558c262e5fab58060/hfarmData/foglio1';
      let body = {
        foglio1: {
          name: descr,
          client: client,
          favorite: fav
        }
      }

      axios.post(url, {
        foglio1: {
          name: descr,
          client: client,
          favorite: fav
        }
      })
      .then((response) => {
        this.openDialog(false);
        this.readData();
      }, (error) => {
        console.log(error);
      });
    }
  },
  components: {
    Card, AddDialog
  }
}
